<template>
  <van-cell @click="playMusicByIndex(position)" :title="title" :label="label" title-class="music-title" label-class="music-author" center/>
</template>

<script>
import {playMusic} from "@/mixin/PayMusic";

export default {
  name: "MusicItem",
  mixins: [playMusic],
  props: {
    title: String,
    label: String,
    id: Number,
    position: Number
  },
  data() {
  },
  methods: {
    play() {
      this.$router.replace({
        path: "/player",
        query: {
          id: this.id
        }
      })
    }
  }
}
</script>

<style>
.music-title {
  font-weight: bold;
}
.music-author{
  font-weight: normal;
}
</style>