import axios from "axios";

export const playMusic = {
    data: {
        playList: [],
        playIndex: 0,
        musicUrl:'',
        musicName: "歌名",
        musicSinger: "歌手",
        musicPic: "https://img.zcool.cn/community/012a4c5d724c27a801202f17b8dbd9.png@1280w_1l_2o_100sh.png",
    },
    methods: {
        playMusicByIndex(index) {
            this.playIndex = index
            axios.get("https://autumnfish.cn/song/url?id=" + this.playList[index].id)
                .then(response => {
                    this.musicUrl = response.data.data[0].url
                })
                .catch(err => {
                    alert("搜索错误" + err)
                })
            axios.get("https://autumnfish.cn/song/detail?ids=" + this.playList[index].id)
                .then(response => {
                    this.musicPic = response.data.songs[0].al.picUrl
                    this.musicSinger = response.data.songs[0].ar[0].name
                    this.musicName = response.data.songs[0].name
                })
                .catch(err => {
                    alert("搜索错误 " + err)
                })
        },
        playMusicByList(list, index) {
            this.playList = list
            this.playIndex = index
            this.playMusicByIndex(index)
        }
    }
}