<template>
  <div>
    <header>
      <van-row align="center">
        <van-col id="menu_circle" span="4">
          <span class="circle"></span>
          <span class="circle"></span>
          <span class="circle"></span>
        </van-col>

        <van-col span="4">
          <van-icon @click="$router.back()" id="left-icon" name="arrow-left"/>
          <van-icon @click="$router.go(1)" id="right-icon" name="arrow"/>
        </van-col>

        <van-col span="8">
          <van-search
              v-model="music"
              shape="round"
              background="transparent"
              placeholder="请输入搜索关键词"
              @search="searchMusic(music)"
          />
        </van-col>

        <van-col span="4"></van-col>
        <van-col span="4">
          登录
          <router-link :to="{path: '/search',query: {keyword:'盗将行'}}">搜索</router-link>
        </van-col>
      </van-row>
      <van-divider/>
    </header>
    <van-row>
      <div class="nav-bar">
        <router-link replace :to="{path:item.path}" v-for="item in navItems" :key="item">{{ item.name }}</router-link>
      </div>
      <div id="main">
        <router-view v-slot="{ Component }">
          <keep-alive>
            <component :is="Component"/>
          </keep-alive>
        </router-view>
      </div>
    </van-row>
    <footer>
      <!--音乐碟片-->
      <div id="audio_disc" ref="audio_disc">
        <van-image id="audio_disc_img" round :src="musicPic"/>
        <span></span>
      </div>
      <!--音乐控制器-->
      <van-row>
        <van-col span="24" style="padding-left: 14px">
          <!--播放进度条-->
          <van-slider v-model="audio_slider" @change="changeAudioProgress" inactive-color="lightgray"
                      active-color="blue" button-size="5px" bar-height="3px">
            <template #button>
              <div>
                <div class="custom-button-bar">{{ transTime(audio_slider) }}</div>
                <span class="circle"></span>
              </div>
            </template>
          </van-slider>
          <!--控制器本体-->
          <van-row align="center">
            <!--左侧区域 歌曲信息-->
            <van-col span="6" style="text-align:start;">
              {{ musicName }}
              {{ musicSinger }}
            </van-col>
            <!--中间区域 控制器按钮-->
            <van-col span="12">
              <!--进度条 其他按钮-->
              <van-row align="center">
                <van-col span="2" style="text-align:end;padding-right: 14px;">

                </van-col>
                <van-col span="20">
                </van-col>
                <van-col span="2" style="text-align:start; padding-left: 14px;">

                </van-col>
              </van-row>

              <!--播放控制按钮-->
              <van-row align="center">
                <van-col span="8">
                  <van-icon
                      @click="prevMusic"
                      class="iconfont icon-skip_previous"
                      size="50px">
                  </van-icon>
                </van-col>
                <van-col span="8">
                  <van-icon v-show="!isPlaying" @click="play" class="iconfont" class-prefix="icon" name="play" size="70px"/>
                  <van-icon v-show="isPlaying" @click="pause" class="iconfont icon-pause" size="70px"/>
                </van-col>
                <van-col span="8">
                  <van-icon @click="nextMusic" class="iconfont icon-skip_next" size="50px"></van-icon>
                </van-col>
              </van-row>
            </van-col>
            <!--右侧区域 其他按钮-->
            <van-col span="6">
              <van-icon class="iconfont icon-playlist" size="50px"></van-icon>
            </van-col>
          </van-row>
        </van-col>
      </van-row>
      <!--播放器本体-->
      <audio
          ref="audio"
          style="display:none;"
          :src="musicUrl"
          @timeupdate="audioTimeupdate"
          @canplay="getDuration"
          @pause="isPlaying=false"
          @playing="isPlaying=true"
          @ended="nextMusic"
          @error="nextMusic"
          controls autoplay>
      </audio>
    </footer>
  </div>
</template>

<script>
import {Toast} from "vant"
import {playMusic} from "@/mixin/PayMusic"

export default {
  name: "App",
  mixins: [playMusic],
  data() {
    return {
      navItems: [
        {name: '首页', path: '/'},
        {name: '播放', path: '/player'}
      ],
      loading: false,
      finished: false,
      music: "",
      musicUrl: "",
      isPlaying: false,
      audio_duration: "0:00",
      audio_currentTime: "0:00",
      audio_slider: 0,
    }
  },
  methods: {
    changeAudioProgress(e) {
      this.$refs.audio.currentTime = e / 100 * this.$refs.audio.duration
    },
    audioTimeupdate(e) {
      this.audio_currentTime = this.transTime(e.target.currentTime)
      this.audio_slider = e.target.currentTime / this.$refs.audio.duration * 100
    },
    getDuration() {
      this.audio_duration = this.transTime(this.$refs.audio.duration)
    },
    transTime(time) {
      let duration = parseInt(time)
      let minute = parseInt(duration / 60)
      let sec = (duration % 60) + ''
      let isM0 = ':'
      if (minute === 0) {
        minute = '00'
      } else if (minute < 10) {
        minute = '0' + minute
      }
      if (sec.length === 1) {
        sec = '0' + sec
      }
      return minute + isM0 + sec
    },
    play() {
      if (this.$refs.audio.currentSrc === "") {
        this.playMusicByIndex(this.playIndex)
      }
      this.$refs.audio.play()
    },
    pause() {
      this.$refs.audio.pause()
    },
    nextMusic() {
      if (this.playIndex === this.playList.length - 1) {
        this.playIndex = 0
      } else {
        this.playIndex++
      }
      this.playMusicByIndex(this.playIndex)
    },
    prevMusic() {
      if (this.playIndex === 0) {
        this.playIndex = this.playList.length - 1
      } else {
        this.playIndex--
      }
      this.playMusicByIndex(this.playIndex)
    },
    searchMusic(keyword) {
      if (keyword === "") {
        keyword = "盗将行"
      }
      this.$router.push({
        path: '/search',
        query: {
          keyword: keyword
        }
      })
    },
    toast(msg) {
      Toast(msg)
    }
  }
}
</script>

