<template>
  <div>
    <h2>热门推荐</h2>
    <van-row>
      <van-col span="6" v-for="item in recommendMusicList" :key="item.id">
        <van-image width="80%" height="70%" radius="18px" fit="cover" :src="item.picUrl"/>
        <div class="van-ellipsis"><b>{{ item.name }}</b></div>
      </van-col>
    </van-row>

    <h2>热门音乐</h2>
    <van-cell @click="pauseM(index)" v-for="(item,index) in recommendNewMusicList" :key="item" center>
      <template #title>
        <div class="song-title">{{ item.name }}</div>
        <div class="song-label">{{ item.song.album.artists[0].name }}</div>
      </template>
      <template #icon>
        <van-image width="60p" height="60px" radius="8px" fit="cover" :src="item.picUrl"/>
      </template>
    </van-cell>
  </div>
</template>

<script>
import {recommendMusicApi, recommendNewMusicApi} from "@/api"
import {playMusic} from "@/mixin/PayMusic";

export default {
  name: "HomePage",
  mixins:[playMusic],
  data() {
    return {
      recommendMusicList: [],
      recommendNewMusicList: [],
    }
  },
  async created() {
    const reList = await recommendMusicApi({limit: 8})
    this.recommendMusicList = reList.data.result
    const songList = await recommendNewMusicApi({limit: 5})
    this.recommendNewMusicList = songList.data.result
  },
  methods:{
    pauseM(index){
      this.list = this.recommendNewMusicList
      this.playMusicByIndex(index)
    }
  }
}
</script>

<style scoped>


.song-title {
  padding-left: 10px;
}

.song-label {
  padding-left: 10px;
}
</style>