<template>
  <div>
    <h2>'{{ keyword }}' 最佳匹配</h2>
    <MusicItem v-for="(item,index) in playList" :key="item.id" :position="index" :id="item.id" :title="item.name"
               :label="item.artists[0].name"></MusicItem>
    <!--<template #icon>
          <van-image width="60p" height="60px" radius="8px" fit="cover" :src="getMusicPic(item.id)"/>
        </template>-->
  </div>
</template>

<script>
import {playMusic} from "@/mixin/PayMusic"
import {searchApi} from "@/api"
import MusicItem from "@/components/MusicItem"

export default {
  name: "SearchPage",
  mixins: [playMusic],
  components: {
    MusicItem
  },
  data() {
    return {
      keyword: this.$route.query.keyword
    }
  },
  computed: {
    getKey() {
      return this.$route.query.keyword
    }
  },
  created() {
  },
  watch: {
    getKey() {
      this.keyword = this.$route.query.keyword
      this.searchList()
    }
  },
  mounted() {
    this.searchList()
  },
  methods: {
    async searchList() {
      let search = await searchApi({keywords: this.keyword}, {limit: 30})
      this.playList = search.data.result.songs
    },
  }
}
</script>

