import {createApp} from 'vue'
import App from './App.vue'
import Router from "@/router";
import '@/assets/font/iconfont.css'
import {
    Button, Row, Col, CellGroup, Cell, List, Search, Icon, Divider, Slider, Image as VanImage,
    Sidebar, SidebarItem,
} from 'vant'

createApp(App)
    .use(Router)
    .use(Button)
    .use(Row)
    .use(Col)
    .use(CellGroup)
    .use(Cell)
    .use(List)
    .use(Search)
    .use(Icon)
    .use(Divider)
    .use(Slider)
    .use(VanImage)
    .use(Sidebar)
    .use(SidebarItem)
    .mount('#app')

document.getElementById("app").style.height = getScreenSize().h - 100 + 'px'

function getScreenSize() {
    if (window.innerWidth) {
        return {
            w: window.innerWidth,
            h: window.innerHeight
        }
    } else if (document.compatMode === "BackCompat") {
        return {
            w: document.body.clientWidth,
            h: document.body.clientHeight
        }
    } else {
        return {
            w: document.documentElement.clientWidth,
            h: document.documentElement.clientHeight
        }
    }
}
